import * as React from "react";
import * as ReactDOM from "react-dom";
import {Block} from './block';
import {Lambda, LambdaFactory} from './lambda';
import {to_string} from './textPrinter';
import * as ev from './evaluate';


interface BlockState {
    evals: Lambda[][];
}

class InputBlock extends React.Component<{}, BlockState> {

    private block: Block;

    constructor(private factory: LambdaFactory, parent?: Block) {
        super();
        this.block = new Block(factory, parent, () => {this.onCompile()})
    }

    private onCompile() {
        let exprs = this.block.expressions();
        this.setState({
            evals: exprs.map((expr) => {
                return ev.reduceAll(expr, ev.normalReduce, this.factory);
            })
        });
    }

    private onSubmit(event: React.KeyboardEvent<HTMLDivElement>) {
        if(event.keyCode == 13) {
            if(event.ctrlKey) {
                this.block.compile();
            } else if(event.shiftKey) {
                this.block.compile();
            }
        }
    }

    render() {
        return(
            <div>
                <div className="inputArea" contentEditable={true} onKeyUp={(o) => this.onSubmit(o)}>a.b.a b</div>
                <div>
                    {this.state.evals.map((lmbs) => {
                        return (
                            <div>
                                {lmbs.map((lmb) => <p>{to_string(lmb)}</p>)}
                            </div>
                            );
                    })}
                </div>
            </div>
        );
    }
}



export class InputPanel extends React.Component<{factory: LambdaFactory}, InputBlock[]> {

    constructor() {
        super();
        this.setState([new InputBlock(this.props.factory)])
    }

    render() {
        return(
            <div className="panel panel-default config-panel">
                <div className="panel-heading">
                    <h3 className="panel-title">
                        <a data-toggle="collapse" data-target=".panel-body">Settings</a>
                    </h3>
                </div>
                <div className="panel-body collapse in">
                    {this.state}
                </div>
            </div>
        );
    }
    
}


